* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
  list-style-position: inside;
}

p {
  line-height: 25px;
}

main {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
}

main .container {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
}

body {
  background: #444;
  color: #f1f1f1;
  font-family: Arial;
}

#root {
  padding: 30px;
}

#root h1,
#root h3 {
  margin-bottom: 15px;
  font-weight: normal;
  letter-spacing: 4px;
}

#root p {
  color: yellow;
  font-size: 24px;
  font-style: italic;
  font-family: cursive;
  letter-spacing: 2px;
  max-width: 675px;
  text-align: justify;
}